<template>
  <div>
    <Heads :title="languages.a12[langIndex]"></Heads>
    <main>
      <section class="floor_num" v-if="!tableshow">
        <van-field
          class="floor_input"
          v-model="tablenum"
          type="number"
          :placeholder="languages.a14[langIndex]"
          :error-message="error"
          input-align="center"
        />
        <div class="floor_btn">
          <van-button @click="returns()" color="#7e7e7e">{{languages.a15[langIndex]}}</van-button>
          <van-button @click="onFloorNum()" color="#dc0000">{{languages.a8[langIndex]}}</van-button>
        </div>
      </section>
      <section class="tables" v-else-if="tableshow">
        <Tables :line="tablenum*1" @tableValue="tableValue" type="input"></Tables>
        <div class="floor_btn">
          <van-button @click="onClearNum()" color="#7e7e7e">{{languages.a15[langIndex]}}</van-button>
          <van-button @click="Generate()" color="#dc0000">{{languages.a20[langIndex]}}</van-button>
        </div>
      </section>
    </main>
  </div>
</template>

<style>
.floor_input .van-field__control {
  border: 0.01rem solid black;
  width: 5.4rem;
  height: 1rem;
  font-size: 0.4rem;
}
</style>
<style scoped>
main {
  width: 100%;
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.floor_num {
  margin-top: 2rem;
}
.floor_btn {
  /* width: 100%; */
  margin-top: 1rem;
  padding: 0 0.8rem 0 0.8rem;
  display: flex;
  justify-content: space-between;
}
.floor_btn .van-button {
  width: 1.8rem;
  height: 0.8rem;
}
.tables {
  margin-top: 1rem;
  margin-bottom: 1.2rem;
}
</style>

<script>
import Heads from "@/components/Head";
import Tables from "@/components/Table";
import arrange from "@/model/arrange";
import { Dialog } from "vant";
import { Encrypt } from "@/model/utils.js";

export default {
  name: "Cop",
  components: { Heads, Tables },
  data() {
    return {
      error: null,
      tableshow: false,
      tabledata: null
    };
  },
  methods: {
    returns() {
      this.$router.back();
    },
    onFloorNum() {
      if (!this.tablenum) {
        this.error = this.languages.a16[this.langIndex];
        return;
      }
      if (this.tablenum == 0) {
        this.error = this.languages.a17[this.langIndex];
        return;
      }
      this.tableshow = true;
    },
    // 接收子组件的数据
    tableValue(val) {
      this.tabledata = val;
    },
    onClearNum() {
      Dialog.alert({
        showCancelButton: true,
        cancelButtonText: this.languages.a23[this.langIndex],
        confirmButtonText: this.languages.a8[this.langIndex],
        message: this.languages.a27[this.langIndex]
      })
        .then(() => {
          this.tablenum = null; // 清空楼层总数
          this.tableshow = false; // 返回
          this.tabledata = null; // 清空table数据
        })
        .catch(() => {});
    },
    Generate() {
      if (this.tabledata === null) {
        Dialog.alert({
          confirmButtonText: this.languages.a8[this.langIndex],
          message: this.languages.a21[this.langIndex]
        });
        return;
      }
      let floorarr = Object.values(this.tabledata);
      let nonull = floorarr.every(ele => ele != null); // 判断楼层名是否有空
      if (!nonull) {
        Dialog.alert({
          confirmButtonText: this.languages.a8[this.langIndex],
          message: this.languages.a22[this.langIndex]
        });
        return;
      }
      let arr = arrange(floorarr);
      let str = "";
      arr.forEach(ele => {
        if (ele[1]) {
          let continuity = `${ele[0]}~${ele[ele.length - 1]};`;
          str = str.concat(continuity);
        } else {
          let onestr = `${ele[0]};`;
          str = str.concat(onestr);
        }
      });
      str = str.slice(0, -1);
      if (new Set(floorarr).size != floorarr.length) {
        Dialog.alert({
          showCancelButton: true,
          cancelButtonText: this.languages.a23[this.langIndex],
          confirmButtonText: this.languages.a8[this.langIndex],
          message: this.languages.a24[this.langIndex]
        })
          .then(() => {
            let obj = { floorMap: str, floorNum: this.tablenum };
            let value = Encrypt(JSON.stringify(obj));
            this.$router.push({
              name: "Copcode",
              params: { encryption: value }
            });
          })
          .catch(() => {});
      } else {
        let obj = { floorMap: str, floorNum: this.tablenum };
        let value = Encrypt(JSON.stringify(obj));
        this.$router.push({ name: "Copcode", params: { encryption: value } });
      }
    }
  },
  computed: {
    languages() {
      return this.$store.state.languages;
    },
    langIndex() {
      return this.$store.state.langIndex;
    },
    tablenum: {
      get() {
        return this.$store.state.tablenum;
      },
      set(val) {
        this.$store.commit("getTablenum", val);
      }
    }
  },
  mounted() {
    if (this.tablenum) {
      this.tableshow = true;
    }
  }
};
</script>