<template>
  <div>
    <table>
      <tr>
        <th>{{languages.a18[langIndex]}}</th>
        <th>{{languages.a19[langIndex]}}</th>
      </tr>
      <tr v-for="count in line" :key="count">
        <td>{{count}}</td>
        <td v-if="!type">fishc_service@126.com</td>
        <td v-else-if="type=='input'">
          <van-field
            class="table_input"
            input-align="center"
            v-model="value[count]"
            @input="changeTable()"
          />
        </td>
      </tr>
    </table>
  </div>
</template>

<style>
.table_input .van-field__control {
  width: 3rem;
  font-size: 0.4rem;
}
</style>

<style scoped>
table {
  border-collapse: collapse;
  border: 0.04rem solid black;
}
th,
td {
  border: 1px solid black;
  padding: 0.2rem 0.4rem;
}

td {
  text-align: center;
}
table {
  border-collapse: collapse;
}
.van-cell {
  padding: 0;
}
</style>

<script>
export default {
  name: "Tables",
  props: ["line", "type"],
  data() {
    return {
      value: {}
    };
  },
  methods: {
    changeTable() {
        this.$emit('tableValue', this.value);
    }
  },
  computed: {
    languages() {
      return this.$store.state.languages;
    },
    langIndex() {
      return this.$store.state.langIndex;
    }
  },
  mounted() {
    for (let i = 1; i <= this.line; i++) {
      this.value[i] = null;
    }
  }
};
</script>