function arrange(source) {
    let t;
    let ta;
    let r = [];
    source.forEach(function(v) {
        // console.log(t, v);
        if (t == v) {
            ta.push(t);
            t++;
            return;
        }
        ta = [v];
        t = v * 1 + 1;
        r.push(ta);
    });
    return r;
}

export default arrange;